import React, { useEffect, useState } from 'react';
import {
  CarouselMenu1,
  CarouselMenu2,
  CarouselMenu3,
  CarouselMenu4,
  CarouselRankMenu,
} from './dummy/dummy';
import * as NetflixStyle from './NetflixStyle.module.scss';
import CarouselComponent from './view/carousel/CarouselComponent';
import CarouselRankComponent from './view/carousel/CarouselRankComponent';
import GnbComponent from './view/gnbmenu/GnbComponent';
import MainMediaComponent from './view/mainMedia/MainMediaComponent';

type Props = {};

export interface CarouselType {
  id: number;
  title: string;
  imgSrc: string;
}

export interface CarouselRankType {
  title: string;
  imgSrc: string;
  rankIconid: string;
  rankIconPath: string;
}

const NetFlixComponent = (props: Props) => {
  // scroll  state (GnbComponent)
  const [isScroll, setIsScroll] = useState(false);

  // scroll    (GnbComponent)
  const handleScroll = () => {
    //  1px  
    if (window.scrollY > 0) {
      setIsScroll(true);
    } else {
      setIsScroll(false);
    }
  };

  // scroll  hook (GnbComponent)
  useEffect(() => {
    window.addEventListener('scroll', handleScroll);

    return () => {
      // dom unMount   
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <div className={NetflixStyle.wrapper}>
      <GnbComponent isScroll={isScroll} />
      <MainMediaComponent />
      <CarouselComponent title="  " data={CarouselMenu1} />
      <CarouselComponent title="  " data={CarouselMenu2} />
      <CarouselComponent title=" " data={CarouselMenu3} />
      <CarouselRankComponent title=" " data={CarouselRankMenu} />
      <CarouselComponent title="  " data={CarouselMenu4} />
    </div>
  );
};

export default NetFlixComponent;
